.header-nav {
  width: 100%;

  flex-grow: 1;
}

.header-nav__wrapper {
  position: absolute;
  top: 78px;
  z-index: 999;

  width: 100%;
}

.header-nav__list {
  display: flex;
  flex-direction: column;
  padding-left: 0;
  margin: 0;
}

.header-nav__item {
  display: flex;
  justify-content: flex-start;
  width: 100%;
  padding-top: 26px;
  padding-bottom: 30px;
  padding-left: 76px;

  text-align: left;
  list-style: none;

  box-shadow: 0 1px 0 0 rgba(236, 236, 236, 1) inset, 0 1px 0 0 rgba(236, 236, 236, 1);
}

.header-nav__link {
  font-size: 17px;
  line-height: 24px;
  font-weight: 700;
  color: #000000;

  &:hover {
    .header-nav__text {
      opacity: 0.6;
    }

    .header-nav__svg {
      opacity: 1;
      fill: #63d1bb;
    }
  }

  &:active {
    opacity: 0.3;
  }
}

.header-nav__svg {
  opacity: 0.2;
  fill: #000000;

  &--search {
    margin-right: 20px;
  }

  &--basket {
    margin-right: 16px;
  }
}

.header-nav__wrapper--extra {
  display: none;
}

.header-nav__toggle {
  position: absolute;
  top: -54px;
  right: 26px;
  left: auto;
  z-index: 2;

  display: block;
  width: 30px;
  height: 30px;

  background-color: #ffffff;

  cursor: pointer;
  border: none;
}

.header-nav__item--search,
.header-nav__item--basket {
  padding-left: 36px;
}

.header-nav--closed .header-nav__toggle::before {
  content: "";

  position: absolute;
  top: 8px;
  left: 5px;

  width: 20px;
  height: 2px;

  background-color: #000000;
  box-shadow: 0 6px 0 0 #000000, 0 12px 0 0 #000000;
}

.header-nav--opened .header-nav__toggle {
  top: -54px;
  right: 26px;
  left: auto;

  &::before,
  &::after {
    content: "";

    position: absolute;
    top: 15px;
    left: 5px;

    width: 20px;
    height: 2px;

    background-color: #000000;
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }

  &:active::before,
  &:active::after {
    background-color: rgba(0, 0, 0, 0.3);
  }
}

@media (max-width: @mobile-width-only) {
  .header-nav--closed .header-nav__list {
    display: none;
  }

  .header-nav--nojs .header-nav {
    position: static;
  }

  .header-nav--opened .header-nav__wrapper {
    z-index: 10;

    min-height: 40vh;

    background-color: #ffffff;
  }

  .header-nav--nojs .header-nav__toggle {
    display: none;
  }

  .header-nav--nojs .header-nav__wrapper {
    position: static;
    min-height: 0;
  }

  .header-nav--nojs .header-nav__list {
    display: block;

    background-color: #ffffff;
  }
}

@media (min-width: @tablet-width) {
  .header-nav {
    width: auto;
  }

  .header-nav__wrapper {
    position: relative;
    top: 0;
  }

  .header-nav__list {
    flex-direction: row;
    justify-content: flex-end;

    &--main {
      position: absolute;
      top: 106px;
      left: -114px;

      order: 2;
    }

    &--tools {
      z-index: 2;

      order: 1;
      margin-right: 52px;
      padding-right: 0;
    }
  }

  .header-nav__item {
    align-items: center;
    width: auto;
    padding: 0;

    box-shadow: none;
  }

  .header-nav__list--main .header-nav__link {
    font-size: 15px;
    line-height: 24px;
  }

  .header-nav__link--basket {
    display: flex;
    align-items: center;
    padding-top: 32px;
    padding-bottom: 34px;

    box-shadow: 1px 0 0 0 rgba(236, 236, 236, 1) inset, 0 1px 0 0 rgba(236, 236, 236, 1);
  }

  .header-nav__link--search {
    padding-top: 36px;
    padding-right: 10px;
    padding-bottom: 24px;
    padding-left: 30px;

    box-shadow: 1px 0 0 0 rgba(236, 236, 236, 1) inset, 0 1px 0 0 rgba(236, 236, 236, 1);
  }

  .header-nav__link--new {
    display: none;
  }

  .header-nav__link--sale {
    display: none;
  }

  .header-nav__link--catalog {
    padding-right: 36px;
  }

  .header-nav__svg--basket {
    margin-right: 34px;
    margin-left: 30px;
  }

  .header-nav__text--search {
    font-size: 0;
  }

  .header-nav__delivery {
    position: absolute;
    top: 16px;
    right: 52px;

    display: block;
    margin: 0;

    font-size: 15px;
    line-height: 24px;
  }

  .header-nav__wrapper--extra {
    display: block;
  }

  .header-nav__toggle {
    display: none;
  }
}

@media (min-width: @desktop-width) {
  .header {
    flex-direction: column;
  }

  .header-nav {
    margin: 0;
  }

  .header-nav__wrapper {
    position: static;

    display: flex;
  }

  .header-nav__list {

    justify-content: flex-start;

    &--main {
      position: static;
      margin: 0;

      order: 0;
    }
  }

  .header-nav__list--main .header-nav__item:nth-child(2) {
    padding-left: 34px;

    box-shadow: 1px 0 0 0 rgba(236, 236, 236, 1) inset, 0 1px 0 0 rgba(236, 236, 236, 1);
  }

  .header-nav__list--main .header-nav__link {
    font-size: 17px;
  }

  .header-nav__list--tools {
    margin: 0;
    margin-left: auto;
  }

  .header-nav__link--search {
    padding-top: 36px;
    padding-left: 34px;

    font-size: 0;
  }

  .header-nav__link--basket {
    padding-top: 32px;
    padding-bottom: 34px;

    box-shadow: 1px 0 0 0 rgba(236, 236, 236, 1) inset, 0 0 0 0 rgba(236, 236, 236, 1);
  }

  .header-nav__wrapper--extra {
    width: 100%;
  }

  .header-nav__list--extra {
    position: absolute;

    display: flex;
    width: 100%;
    margin: 0;
    margin-top: 16px;
  }

  .header-nav__link--new {
    display: block;
    padding-right: 60px;

    font-size: 15px;
  }

  .header-nav__link--sale {
    display: block;

    font-size: 15px;
  }

  .header-nav__item--delivery {
    display: flex;

    position: absolute;
    right: 0;
  }

  .header-nav__delivery {
    position: static;
  }

  .header-nav__toggle {
    display: none;
  }
}
